.networkBuilder{
    border-top: solid 4px #ffb300;
    width: 100%;

}

.networkBuilder .networkBuilderBody{
    display: flex;
    flex-direction: row;
    flex: max-content;
    gap: 10px;
}

.networkBuilder .networkBuilderBody .graphView {
    /*min-height: 500px;*/
    min-width: 1000px;
    border-left: solid 4px #000000;
    flex-grow:2;
    flex-basis: auto;
}

.networkBuilder .networkBuilderBody .picker{
    border-right: solid 1px #000000;
    flex-grow:1;
    flex-basis: auto;
}

.networkBuilder .simulateNetworkButton{
    background-color: #ffb300;
    top:100px;
    margin: 0;
    left: auto;
    bottom: auto;
    right:20px;
    z-index: 1000;
    position:absolute;
}

.operationsNav{
    padding: 10px;
    overflow: hidden;
    background-color: transparent;
    /*border-bottom: solid 1px #000000;*/
}
