.cellTypePicker{
    alignment: left;
    max-width: 100%;
    min-height: 800px;
    min-width: 250px;
    padding-left: 10px;
    padding-right: 10px;


}

.pickerTitle{
    font-family: 'Exo 2', sans-serif;
    font-size: 1.7vw;
}

.createCellTypeBtn{
    float: bottom;
    position:absolute;
    left: 1vw;
    text-overflow: ellipsis;
}

.cellTypesList{
    max-height: 450px;
    min-height: 450px;
    min-width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

.deleteCellTypeButton{
    position: absolute;
    top: 5px;
    right: 5px;
    opacity: 0.4;
}

.cellType-active{
    background-color: #ffb300;
}

