.view-wrapper {
  height: 100%;
  width: 100%;
  margin: 0;
  display: flex;
  box-shadow: none;
  background: #f9f9f9;
  transition: opacity 0.167s;
  opacity: 1;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .view-wrapper > .graph {
    align-content: stretch;
    flex: 1 1;
    min-width: 100%;
    width: 1200px;
    height: 80%; }
  .view-wrapper .node .shape > use.node {
    color: #ffb300;
    stroke: black;
    fill: white;
    -webkit-filter: url(#dropshadow);
            filter: url(#dropshadow);
    stroke-width: 0.5px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
    .view-wrapper .node .shape > use.node.hovered {
      stroke: #ffb300; }
    .view-wrapper .node .shape > use.node.selected {
      color: white;
      stroke: #ffb300;
      stroke-width: 1px;
      fill: #ffb300; }
  .view-wrapper .node .node-text {
    fill: black;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
    .view-wrapper .node .node-text.selected {
      fill: white;
      stroke: white; }
  .view-wrapper .edge {
    color: white;
    stroke: #ffb300;
    stroke-width: 2px;
    marker-end: url(#end-arrow);
    cursor: pointer; }
    .view-wrapper .edge .edge-text {
      stroke-width: 0.5px;
      fill: #ffb300;
      stroke: #ffb300;
      cursor: pointer;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none; }
    .view-wrapper .edge.selected {
      color: #ffb300;
      stroke: #ffb300; }
      .view-wrapper .edge.selected .edge-text {
        fill: white;
        stroke: white; }
  .view-wrapper .edge-mouse-handler {
    stroke: black;
    opacity: 0;
    color: transparent;
    stroke-width: 15px;
    cursor: pointer;
    pointer-events: all; }
  .view-wrapper .arrow {
    fill: #ffb300; }
  .view-wrapper .graph-controls {
    position: absolute;
    bottom: 30px;
    right: 15px;
    z-index: 100;
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 15px;
    align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
    .view-wrapper .graph-controls > .slider-wrapper {
      background-color: white;
      color: #ffb300;
      border: solid 1px lightgray;
      padding: 6.5px;
      border-radius: 2px;
      horiz-align: right;
      float: right; }
      .view-wrapper .graph-controls > .slider-wrapper > span {
        display: inline-block;
        vertical-align: top;
        margin-top: 2px; }
      .view-wrapper .graph-controls > .slider-wrapper > .slider {
        position: relative;
        margin-left: 4px;
        margin-right: 4px;
        cursor: pointer; }
    .view-wrapper .graph-controls > .slider-button {
