.col-form-label{
    -webkit-text-fill-color: #31373e;
    font-family: 'Exo 2', sans-serif;
    font-size: 1.2vw;
}

.title{
    font-family: 'Exo 2', sans-serif;
    font-size: 1.7vw;
    text-align: center;
    text-decoration: underline;
}

.createCellForm.title{
    font-family: 'Exo 2', sans-serif, underline;
}

.textField{
    border-radius:3rem;
}

.btn{
    border-radius:2rem;
    font-family: 'Exo 2', sans-serif;
    font-size: 1.20vw;
    height: max-content;
    margin: auto;
}

#blockMath{
    margin: auto;
    align-content: center;
    alignment: center;
    align-self: center;
    align-items: center;
}

#createCellForm{
    /*border-radius:2rem;*/

    /*background-color: #ffe8bb;*/
    background-color: rgba(255, 245, 214, 0.78);
    padding:12px 12px 12px 12px;
    margin: auto;


    border: solid 4px #ffb300;
}

.draggableElement{
    display: block;
    z-index: 5;
}

.backgroundElement{
    display: block;
    position: relative;
    top: -23px;
    z-index: 3;
}